<template>
  <div>
    <div class="text-right mt-4">
      <button class="btn btn-primary" @click="openModal(true)">
          </button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
            <th></th>
            <th width="200"></th>
            <th width="200"></th>
            <th width="150"></th>
            <th width="120"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for=" item in coupons" :key="item.id">
          <td>{{item.title}}</td>
          <td>{{item.percent}}%</td>
          <td>{{item.due_date}}</td>
          <td>
              <span v-if="item.is_enabled" class="text-success"></span>
              <span v-else></span>
          </td>
          <td>
              <div class="btn-group">
                  <button class="btn btn-outline-info btn-sm" @click="openModal(false, false, item)"></button>
                  <button class="btn btn-outline-danger btn-sm" @click="openModal(false, true, item)"></button>
              </div>

          </td>
        </tr>
      </tbody>
    </table>
    <!--Pagination-->
    <Pagination :pages="pagination" @emit-page="getCoupon"></Pagination>
    <!-- Modal -->
    <div class="modal fade" id="couponModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog">
        <div class="modal-content border-0">
            <div class="modal-header bg-dark text-white">
              <h5 class="modal-title" id="exampleModalLabel">
                  <span v-if="isNew"></span>
                  <span v-else></span>
              </h5>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              <div class="form-group">
                  <label for="title"></label>
                  <input type="text" class="form-control" id="title" placeholder="" v-model="tempCoupon.title">
              </div>
              <div class="form-group">
                  <label for="code"></label>
                  <input type="text" class="form-control" id="code" placeholder="" v-model="tempCoupon.code">
              </div>
              <div class="form-group">
                  <label for="due_date"></label>
                  <input type="text" class="form-control" id="due_date" placeholder="" v-model="tempCoupon.due_date">
              </div>
              <div class="form-group">
                  <label for="percent"></label>
                  <input type="number" class="form-control" id="percent" placeholder="" v-model="tempCoupon.percent">
              </div>
              <div class="form-check">
                  <input class="form-check-input" type="checkbox" id="is_enabled" :true-value="1" :false-value="0" v-model="tempCoupon.is_enabled">
                  <label class="form-check-label" for="is_enabled"></label>
              </div>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-secondary" data-dismiss="modal"></button>
              <button type="button" class="btn btn-primary" @click="updateCoupon()"></button>
            </div>
        </div>
      </div>
    </div>
    <div class="modal fade" id="delCouponModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
            <div class="modal-header bg-danger text-white">
              <h5 class="modal-title" id="exampleModalLabel">
                  <span></span>
              </h5>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
                 <strong class="text-danger">{{ tempCoupon.title }}</strong> ()
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
              <button type="button" class="btn btn-danger" @click="removeCoupon"></button>
            </div>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
import $ from 'jquery';
import Pagination from '@/components/Pagination';

export default {
    data() {
        return {
            coupons: [],
            tempCoupon: {},
            isNew: true,
            pagination: {},
            delCoupon: false
        }
    },
    methods: {
        getCoupon(page = 1) {
          const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupons?page=${page}`
          const vm = this;
          vm.$store.state.isLoading = true;
          this.$http.get(api).then((response) => {
              vm.$store.state.isLoading = false;
              console.log(response.data);
              vm.coupons = response.data.coupons;
              vm.pagination = response.data.pagination;
              vm.coupons.forEach((item) => {
                  const dates = new Date(item.due_date * 1000);
                  const year = dates.getFullYear();
                  const month = dates.getMonth() + 1;
                  const date = dates.getDate();
                  item.due_date = `${year}/${month}/${date}`
              }) //Unix Timestamp
              // console.log(vm.coupons);
          })
        },
        openModal(isNew, delCoupon, item) {
          if (isNew) {
              this.tempCoupon = {};
              this.isNew = true;
              $('#couponModal').modal('show');
          } else if (!isNew && !delCoupon) {
              this.tempCoupon = Object.assign({}, item);
              this.isNew = false;
              $('#couponModal').modal('show');
          } else {
              this.tempCoupon = item;
              this.delCoupon = true;
              $('#delCouponModal').modal('show');
          }
        },
        updateCoupon() {
          let api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupon`;
          let httpMethod = 'post';
          const vm = this;
          vm.$store.state.isLoading = true;
          vm.tempCoupon.due_date = Math.floor(new Date(vm.tempCoupon.due_date) / 1000);
          if (!vm.isNew) {
              api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
              httpMethod = 'put';
          }
          this.$http[httpMethod](api, {
              data: vm.tempCoupon
          }).then((response) => {
              vm.$store.state.isLoading = false;
              console.log(response.data);
              vm.getCoupon();
              $('#couponModal').modal('hide');
          })
        },
        removeCoupon() {
          const vm = this;
          const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
          vm.$http.delete(api).then((response) => {
              if (response.data.success) {
                  $('#delCouponModal').modal('hide');
                  vm.getCoupon();
                  console.log(response.data.message)
              } else {
                  $('#delCouponModal').modal('hide');
                  vm.getCoupon();
                  console.log(response.data.message)
              }
          })
        },
    },
    components: {
      Pagination
    },
    created() {
      this.getCoupon();
    }

}
</script>